				"html": "<p>We can also delete properties from objects like this:</p>\n<pre><code>delete ourDog.bark;\n</code></pre>\n<p>The <strong>delete operator</strong> removes a property from an object.</p>\n<h2>Syntax</h2>\n<p><code>delete expression</code> where expression should evaluate to a property reference, e.g.:</p>\n<pre><code>delete object.property\ndelete object['property']\n</code></pre>\n<h2>Parameters</h2>\n<p><strong>object</strong> The name of an object, or an expression evaluating to an object.</p>\n<p><strong>property</strong> The property to delete.</p>\n<h2>Return value</h2>\n<p>Throws in <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Functions_and_function_scope/Strict_mode\">strict</a> mode if the property is an own non-configurable property (returns false in non-strict). Returns true in all other cases.</p>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/delete\">Read more</a></p>",
